<template>
	<view class="content">
		<view>
			<u-modal :show="show" @confirm="show = false" :title="brand.brand" :content='brand.info'></u-modal>
		</view>
		<view class="title">
			<span @click="toBrand()">< 返回</span>
			<span>{{brand.brand}}</span>
			<span @click="show = true">品牌介绍</span>
		</view>
		<view class="swiper-frame">
			<u-swiper
				class="swiper"
			    :list="carHotList"
				keyName="img"
			    indicator
			    indicatorMode="line"
				height="210"
				interval="5000"
			    circular
			></u-swiper>
			<view class="swiper-text">
				<u-text class="text" :lines="3" :text="carHotList[idx].info" size="8"></u-text>
				<span class="count">租用次数:{{carHotList[idx].count}}</span>
				<span class="price">日租:￥{{carHotList[idx].price}}</span>
			</view>
		</view>
		<view class="list">
			<view class="type-list">
				<span :class="key == type ? 'type' : ''" v-for="(item, key) in typeList" @click="switchTab(key)">{{item}}</span>
			</view>
			<view class="card-list" v-show="type == 0 || type == 1">
				<span>轿车</span>
				<view class="card-item" v-for="(item, key) in vehicleModelList" v-if="item.type == '轿车'" @click="toCar(item)">
					<image :src="item.img"></image>
					<view class="bottom">
						<span class="name">{{item.model}}</span>
						<br>
						<p class="market-price">市售价：{{item.marketPrice}} </p>
						<view class="bottom-label">
							<view class="label vip">
								<image src="../../static/details/vip.png"></image>
								<span>会员免押</span>
							</view>
							<view class="label car-green" v-if="item.energy == '插混' || item.energy == '增程' || item.energy == '纯电' ">
								<image src="../../static/green_car.png"></image>
								<span>新能源</span>
							</view>
						</view>
					</view>
				</view>
			</view>
			<view class="card-list" v-show="type == 0 || type == 2">
				<span>SUV</span>
				<view class="card-item" v-for="(item, key) in vehicleModelList" v-if="item.type == 'SUV'" @click="toCar(item)">
					<image :src="item.img"></image>
					<view class="bottom">
						<span class="name">{{item.model}}</span>
						<br>
						<p class="market-price">市售价：{{item.marketPrice}} </p>
						<view class="bottom-label">
							<view class="label vip">
								<image src="../../static/details/vip.png"></image>
								<span>会员免押</span>
							</view>
							<view class="label car-green" v-if="item.energy == '插混' || item.energy == '增程' || item.energy == '纯电' ">
								<image src="../../static/green_car.png"></image>
								<span>新能源</span>
							</view>
						</view>
					</view>
				</view>
			</view>
			<view class="card-list" v-show="type == 0 || type == 3">
				<span>MPV</span>
				<view class="card-item" v-for="(item, key) in vehicleModelList" v-if="item.type == 'MPV'" @click="toCar(item)">
					<image :src="item.img"></image>
					<view class="bottom">
						<span class="name">{{item.model}}</span>
						<br>
						<p class="market-price">市售价：{{item.marketPrice}}</p>
